<template>
  <v-app>
    <link href='https://fonts.googleapis.com/css?family=Roboto:300,400,500,700|Material+Icons' rel="stylesheet">
    <main>
      <v-container fluid>
        <v-tabs dark fixed centered v-model="tab">
    <v-toolbar class="cyan" dark>
      <v-toolbar-title class="display-2">
        <i class="qtum-icon qtum-icon-logo"></i>Qtum
      </v-toolbar-title>
      <template v-if="wallet">
        <v-spacer></v-spacer>
        <v-toolbar-items class="hidden-sm-and-down">
          <v-list dark>
            <v-list-tile style="float:left">
              <v-list-tile-content>
                <v-list-tile-title>{{ $t('home.info.address') }}</v-list-tile-title>
                <v-list-tile-sub-title>{{wallet.info.address}}</v-list-tile-sub-title>
              </v-list-tile-content>
            </v-list-tile>
            <v-list-tile style="float:left">
              <v-list-tile-content>
                <v-list-tile-title>{{ $t('home.info.balance') }}</v-list-tile-title>
                <v-list-tile-sub-title>{{wallet.info.balance}}</v-list-tile-sub-title>
              </v-list-tile-content>
            </v-list-tile>
            <v-list-tile style="float:left">
              <v-list-tile-content>
                <v-list-tile-title>{{ $t('home.info.unconfirmed_balance') }}</v-list-tile-title>
                <v-list-tile-sub-title>{{wallet.info.unconfirmedBalance}}</v-list-tile-sub-title>
              </v-list-tile-content>
            </v-list-tile>
          </v-list>
        </v-toolbar-items>
      </template>
    </v-toolbar>
    <v-tabs-bar class="cyan">
      <v-tabs-slider class="yellow"></v-tabs-slider>
      <template v-if="wallet == false">
        <v-tabs-item href="create">
          {{ $t('common.create') }}
        </v-tabs-item>
        <v-tabs-item href="restore">
          {{ $t('common.restore') }}
        </v-tabs-item>
      </template>
      <template v-else>
        <v-tabs-item href="view">
          {{ $t('common.view') }}
        </v-tabs-item>
        <v-tabs-item href="send">
          {{ $t('common.send') }}
        </v-tabs-item>
      </template>
    </v-tabs-bar>
    <v-tabs-items>
      <template v-if="wallet == false">
        <v-tabs-content id="create">
          <create-wallet @created="setWallet"></create-wallet>
        </v-tabs-content>
        <v-tabs-content id="restore">
          <restore-wallet @restored="setWallet"></restore-wallet>
        </v-tabs-content>
      </template>
      <template v-else>
        <v-tabs-content id="view">
          <view-wallet></view-wallet>
        </v-tabs-content>
        <v-tabs-content id="send">
          <send @send="setWallet"></send>
        </v-tabs-content>
      </template>
    </v-tabs-items>
  </v-tabs>
      </v-container>
      <notify></notify>
    </main>
  </v-app>
</template>

<script>
import notify from './components/Notify'
import CreateWallet from './components/wallet/Create'
import RestoreWallet from './components/wallet/Restore'
import ViewWallet from './components/wallet/View'
import Send from './components/Send'
import webWallet from './web-wallet'

export default {
  name: 'app',
  data () {
    return {
      wallet: false,
      tab: 'create'
    }
  },
  components: {
    notify,
    CreateWallet,
    RestoreWallet,
    ViewWallet,
    Send,
  },
  methods: {
    setWallet() {
      this.wallet = webWallet.getWallet()
      this.tab = 'view'
    }
  }
}
</script>



